              <Certificate title="Gestion de projet Parcours Classique" href="https://drive.google.com/file/d/1byWukZHf3--FkBdujgLQnIZx3gTEqmEO/view" subtitle1="Centralelille" subtitle2="Le titulaire de cette attestation est capable de concevoir et piloter un projet, d’animer une réunion ..." imgsrc={person4} imgpath={person4Full} />
