import ServiceComponent from './ServiceComponent'
import React from 'react'

class ServiesComponent extends React.Component {
    render() {
        return (
            <section id="services" className="section section-gray ">
                <div className="container clearfix">
                    <div className="row services">
                        <div className="col-md-12">
                            <h2 className="heading">Services</h2>
                            <div className="row">
                                <ServiceComponent icon="ti-desktop" title="La création et la conception des sites web" text="La création de site web inclura toutes les fonctions de base et avancées que vous souhaitez." />
                                <ServiceComponent icon="ti-printer" title="Développement d'applications de bureau" text="Afin d'utiliser de la façon la plus efficace possible les ressources à disposition." />
                                <ServiceComponent icon="ti-search" title="Création et conception des applications mobiles." text="Vous permettre de mettre toutes les fonctionnalités des smartphones au service de votre business." />
                            </div>
                            <div className="row">
                                <ServiceComponent icon="ti-comments" title="Création des cartes de visite." text="Démarquez-vous en créant vos propres cartes de visite avec une conception personnalisée." />
                                <ServiceComponent icon="ti-email" title="Création des logos professionnels" text="Grande sélection de logos. Chaque logo est personnalisé pour votre organisation ou votre société." />
                                <ServiceComponent icon="ti-layout-sidebar-left" title="Création des affiches publicitaires" text="Création graphique d'affiche pour votre communication, adapté à votre message et votre charte
                                        graphique."/>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default ServiesComponent;