import React from 'react'

class CertificateComponent extends React.Component {

  constructor(props)
    {
      super(props)
    }
  render() {
    return (
      <div className="col-md-3 col-sm-6 wow slideInDown" data-wow-delay="0.3s">
                <div className="team-member">
                  <div className="image">
                    <span>
                      <img src={this.props.imgsrc} path={this.props.imgpath} alt="" className="img-responsive myImg-cert" />
                    </span>
                  </div>
                  <h3>
                    <a href={this.props.href}>{this.props.title}</a>
                  </h3>

                  <p className="role">{this.props.subtitle1}</p>
                  <div className="ligne">
                  </div>
                  <div className="text">
                    <p>{this.props.subtitle2}</p>
                  </div>
                </div>
              </div>
    );
  }

}

export default CertificateComponent;