import React from 'react'
class ProjetComponent extends React.Component {

    constructor(props)
    {
        super(props)
    }
    render() {
        return (
            <div className="col-sm-4 col-md-3">
            <div className="box container_projects myImg"
                title={this.props.title}>
                <img src={this.props.img} alt="" className="img-responsive image_projects" />
                <div className="overlay_projects">
                    <span className="icon_projects" title="AutoConsultation">
                        <i className="fa fa-eye"></i>
                    </span>
                </div>
            </div>
        </div>
        );
    }
}
export default ProjetComponent;